*{
  background-color: rgb(250, 250,250);

  justify-content: center;
  align-content: center;
}

.container {
  display: flex;
  align-items: center;
  flex-direction: column;
  background-color: black;
}

@media only screen and (max-width: 770px) {
  .container {
    margin: 0 10%;
  }

  .image {
    width: 100%;
  }
}

